import React from 'react';

class Cell extends React.PureComponent {
  render() {
    const { isRowHeader = false, value } = this.props;
    const style = isRowHeader ?
      `ln-c-table__header-cell ln-u-clean ln-u-bg-color-white ln-u-h6`
      :
      `ln-c-table__cell tu-c-size-guide__table--cell`;

    return (<td className={`${style} ln-u-text-align-center`}>
      {value ? value.toUpperCase() : ''}
    </td>);
  }
}

class Header extends React.PureComponent {
  render() {
    const { isStandardTable = false, value } = this.props;
    const style = isStandardTable ? `ln-u-clean` : `ln-u-border-bottom ln-u-bg-color-black tu-u-color-white`;

    return (<th className={`${style} ln-c-table__header-cell ln-u-text-align-center ln-u-h6`}>
      {value ? value.toUpperCase() : ''}
    </th>);
  }
}

const Row = ({ isHeader = false, children }) => {
  const style = isHeader ? `ln-c-table__header-row` : ``;
  return (<tr className={`${style} ln-c-table__row`}>{children}</tr>);
}

const Table = ({ data }) => {

  // Get text value for each node
  const getValue = (obj) => obj.value ? obj.value : '';

  const renderHead = (data) => {
    const { columnHeader, rows, columns } = data;

    return (<thead className='ln-c-table__header ln-u-clean'>
      <Row isHeader={true}>
        {rows ? <Header isStandardTable={false} value={columnHeader} /> : null}
        {columns.map((column, index) => {
          return (<Header isStandardTable={rows ? false : true} key={index}
            value={getValue(column)} />);
        })}
      </Row>
    </thead>);
  }

  const renderBody = (data) => {

    // For provided rowIndex, get all <td> nodes
    const getCells = ({ rowIndex, data }) => {
      const { columns, values } = data;

      // For provided rowIndex, get row data
      const rowData = values[rowIndex] ? values[rowIndex] : [];
      return rowData && rowData.length ?
        (columns.map((column, index) => {
          return (<Cell key={index} value={getValue(rowData[index])} />);
        }))
        :
        null;
    }

    const getRowHeaderCells = (data) => {
      const { rowHeader, columns } = data;
      const rowHeaderNode = (<Cell isRowHeader={true} key={0} value={rowHeader} />);

      return (<Row key={0}>
        {[rowHeaderNode, ...columns.map((column, index) => {
          return (<Cell key={index + 1} value='' />)
        })]}
      </Row>);
    }

    const getRows = (data) => {
      const { values, rows } = data;

      return rows ?
        // Two-way lookup table
        ([getRowHeaderCells(data), ...rows.map((row, rowIndex) => {
          return (<Row key={rowIndex + 1}>
            {<Cell isRowHeader={true} value={getValue(rows[rowIndex])} />}
            {getCells({ rowIndex, data })}
          </Row>);
        })])
        :
        // Standard table
        (values.map((row, rowIndex) => {
          return (<Row key={rowIndex}>
            {getCells({ rowIndex, data })}
          </Row>);
        }))
    }

    return (<tbody className='ln-c-table__body'>
      {getRows(data)}
    </tbody>);
  }

  return (<table className='ln-c-table tu-c-size-guide__table'>
    {renderHead(data)}
    {renderBody(data)}
  </table>);
}

export default Table;
