import React from 'react';
import Overlay from '../Overlay/Overlay';
import SizeGuideWrapper from './presentational/SizeGuideWrapper';
import util from './config/util';
import tealiumUtil from '../metrics/tealium';

class SizeGuideContainer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      active: false,
      category: ''
    };
  }

  componentDidMount() {
    const sizeGuideLinkNode = document.getElementById('size-guide-link');
    if (sizeGuideLinkNode) {
      sizeGuideLinkNode.addEventListener('click', this.handleLinkClick, true);
      const { coreCategory, categoryList } = sizeGuideLinkNode.dataset;
      const category = util.getCategoryType({ coreCategory, categoryList });
      this.setState({ category });
    }
  }

  componentWillUnmount() {
    const sizeGuideLinkNode = document.getElementById('size-guide-link');
    if (sizeGuideLinkNode) {
      sizeGuideLinkNode.removeEventListener('click', this.handleLinkClick, true);
    }
  }

  handleLinkClick = () => {
    this.toggleSizeGuide();
    tealiumUtil.trigger({ type: 'sizeGuide' });
  }

  toggleSizeGuide = () => this.setState(prevState => ({ active: !prevState.active }));

  render() {
    const { active, category } = this.state;
    const headers = util.getHeaderData();
    const data = util.getCategoryData(category);
    return data
      ? (
        <React.Fragment>
          <Overlay active={active} onOverlayClick={this.toggleSizeGuide} />
          <SizeGuideWrapper active={active} category={{ headers, data }} onClose={this.toggleSizeGuide} />
        </React.Fragment>
      )
      : null;
  }
}

export default SizeGuideContainer;
