import categoryMap from './categoryMap';
import categoryData from './categoryData';

const util = {
  /*
  Get size guide category type when provided with a core-category and a list of comma-separated categories
  Example:
  Input: core-category='men', category-list='men-casual-shirts,men-coats-and-jackets'
  Output: 'E'
  */
  getCategoryType: ({ coreCategory, categoryList }) => {

    const getCategory = ({ coreCategoryType, categoryList }) => {
      const categoryListArray = categoryList ? categoryList.split(',').filter(value => value !== '') : [];
      const category = categoryListArray.reduce((acc, categoryType) => {
        return acc ? acc : categoryMap[coreCategoryType][categoryType.toLowerCase()];
      }, undefined);

      // Show fallback for core-category if category-list doesn't have corresponding size guide category type
      return category ? category : categoryMap[coreCategoryType]['all'];
    };

    const coreCategoryType = coreCategory ? coreCategory.toLowerCase() : '';

    // Check if core-category is non-empty and is of allowed type
    return coreCategoryType && Object.keys(categoryMap).includes(coreCategoryType) ?
      (['kids', 'baby'].includes(coreCategoryType) ?
        categoryMap[coreCategoryType]['all'] : getCategory({ coreCategoryType, categoryList }))
      :
      ('');
  },
  // Get size guide category data for a given category type, eg: 'E'
  getCategoryData: (category) => {
    const { categories } = categoryData;
    const data = categories.find((sizeGuideCategory) => sizeGuideCategory.category === category.toUpperCase());
    return data ? data : null;
  },
  getHeaderData: () => categoryData.headers
}

export default util;
