import React from 'react';

const Button = ({ id, text, selected, onButtonClick }) => {
  const style = selected ? '' : 'tu-c-size-guide__filter-button--disabled';
  return (<button className={`ln-c-button ln-c-button--secondary 
  tu-c-size-guide__filter-button ${style}`}
    onClick={() => onButtonClick(id)}>
    {text ? text.toUpperCase() : ''}
  </button>);
}

export default Button;
