import React from 'react';
import Slider from '../../CPM/presentational/Slider';
import Table from './Table';

class TableWrapper extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      sliderState: false
    }
  }

  handleToggle = (name, value) => {
    value === 'true' ? this.setState({ sliderState: true }) : this.setState({ sliderState: false });
  }

  getTableData = () => {
    const { content } = this.props;
    const { data } = content;
    const { rowHeader, columnHeader, rows, columns, values } = data;

    const { sliderState } = this.state;
    const metric = sliderState ? 'inch' : 'cm';

    const getValues = ({ arr = [], metric }) => {
      return arr ?
        (arr.map((obj) => {
          return obj && obj.measurement ?
            ({ value: obj.isMetric ? obj.measurement[metric] : obj.measurement.size })
            :
            ({ value: '' });
        }))
        :
        null;
    }

    return {
      rowHeader,
      columnHeader,
      rows: getValues({ arr: rows, metric }),
      columns: getValues({ arr: columns, metric }),
      values: values ? values.map((value) => getValues({ arr: value, metric })) : null
    };
  }

  render() {
    const { content } = this.props;
    const { id, title, toggleMetric, data } = content;

    const sliderStyle = 'ln-c-form-slider--sizeguide';

    return (<div className='ln-u-push-top ln-u-push-bottom'>
      <div className={`ln-u-display-flex ln-u-justify-content-space-between
      ${data.rows ? 'ln-u-push-bottom' : ''}`}>
        {<div className='ln-u-h6'>{title ? title.toUpperCase() : ''}</div>}
        {id && toggleMetric && <Slider id={id} name='' title='' value='false'
          altStyle={sliderStyle} onToggle={this.handleToggle} />}
      </div>
      {data && <Table data={this.getTableData()} />}
    </div>);
  }
}

export default TableWrapper;
