import React from 'react';
import SizeGuide from './SizeGuide';
import DressFitGuide from './DressFitGuide';
import HowToMeasure from './HowToMeasure';
import Icon from '../../paymentDetails/presentational/Icon';

const SizeGuideWrapper = ({ active, category, onClose }) => {
  const { headers, data } = category;
  const { sizeGuide, dressFitGuide, howToMeasure } = data;
  const activeStyle = active ? 'active' : '';

  return (<div className={`tu-c-size-guide ${activeStyle}`}>
    <div className='ln-u-soft*1/2 ln-u-display-flex ln-u-justify-content-flex-end'>
      <button type='button' onClick={onClose}
        className='ln-c-icon ln-c-icon--large tu-c-size-guide__close-button'>
        <Icon classes='ln-c-icon ln-c-icon--large' icon='cross' viewBox='-17 -17 66 66' />
      </button>
    </div>
    <div className='ln-u-soft'>
      {sizeGuide && <SizeGuide header={headers.sizeGuide} showLink={howToMeasure ? true : false} data={sizeGuide} />}
      {dressFitGuide && <DressFitGuide header={headers.dressFitGuide} data={dressFitGuide} />}
      {howToMeasure && <HowToMeasure header={headers.howToMeasure} data={howToMeasure} />}
    </div>
  </div>);
}

export default SizeGuideWrapper;
