import React from 'react';
import TableWrapper from './TableWrapper';

const DressFitGuide = ({ header, data }) => {
  const { title, description } = header;

  return (<React.Fragment>
    <div className='ln-u-h4 ln-u-flush-bottom'>{title}</div>
    <div>{description}</div>
    {data && data.content && data.content.length ?
      data.content.map((tableContent, index) => {
        return <TableWrapper key={index} content={tableContent} />;
      })
      :
      null}
  </React.Fragment>);
}

export default DressFitGuide;
