import React from 'react';
import Icon from '../paymentDetails/presentational/Icon';

class Accordion extends React.Component {

  constructor(props) {
    super(props);
    const { defaultOpen } = props;
    defaultOpen ? this.state = { open: defaultOpen } : this.state = { open: false };
  }

  handleClick = () => this.setState((prevState) => { return { open: !prevState.open } });

  render() {
    const { title, body, borderStyle } = this.props;
    const { open } = this.state;
    const openStyle = open ? 'is-open' : '';
    return (<div className={`ln-c-foldable ${borderStyle ? borderStyle : 'ln-c-foldable--bordered'} ${openStyle}`}>
      <div className='ln-c-foldable__head'>
        <button className='ln-c-foldable__toggle' aria-hidden='true' onClick={this.handleClick}>
          <Icon classes='ln-c-icon ln-c-foldable__chevron' icon='chevlon-down' viewBox='0 0 185.344 185.344' />
        </button>
        <div className='ln-c-foldable__title'>
          {title}
        </div>
      </div>
      <div className='ln-c-foldable__body'>
        {body}
      </div>
    </div>);
  }
}

export default Accordion;
