import React from 'react';
import Section from './Section';
import TableWrapper from './TableWrapper';

const SizeGuide = ({ header, showLink, data }) => {

  const renderSections = (data) => {
    const { sections } = data;
    const singleSection = sections.length === 1 ? sections[0] : null;
    return singleSection ?
      (singleSection.body && singleSection.body.content && singleSection.body.content.length ?
        singleSection.body.content.map((tableContent, index) => {
          return <TableWrapper key={index} content={tableContent} />;
        })
        :
        null)
      :
      (sections.map((section, index) => {
        return (<div key={index} className='ln-u-push-top ln-u-push-bottom'>
          <Section section={section} />
        </div>);
      }))
  }

  const { title, sections } = data;
  const { description, link } = header;

  return (<React.Fragment>
    <div className='ln-u-h2 ln-u-flush-bottom'>{title}</div>
    <div className='ln-u-push-bottom'>{description}</div>
    {showLink && <a className='ln-c-standalone-link' href='#howToMeasure'>{link}</a>}
    {sections && renderSections(data)}
  </React.Fragment>);
}

export default SizeGuide;
