import React from 'react';
import PropTypes from 'prop-types';

const Slider = ({ id, title, name, value, onToggle, altStyle }) => {
  return (
    <li className="ln-o-bare-list__item">
      <label className={`ln-c-form-slider ${altStyle ? altStyle : ''}`} htmlFor={id}>
        <span className="ln-c-form-slider__title ln-u-font-size-h5">{title}</span>
        <input
          type="checkbox"
          id={id}
          name={id}
          className="ln-u-hidden"
          onChange={e => {
            const changeValue = e.target.checked ? 'true' : 'false';
            onToggle(name, changeValue);
          }}
          defaultChecked={(value === 'true' || value === 'li') ? true : false}
        />
        <span className="ln-c-form-slider__mechanism">
          <span className="ln-c-form-slider__state"></span>
        </span>
      </label>
    </li>
  );
};

Slider.propTypes = {
  id: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired,
  name: PropTypes.string.isRequired,
  value: PropTypes.string.isRequired,
  onToggle: PropTypes.func.isRequired
};

export default Slider;
