import React from 'react';

const HowToMeasure = ({ header, data }) => {

  const renderImageMap = (data) => {
    return data && data.image ? (<div className='ln-u-push-top ln-u-push-bottom'>
      <img src={require(`../images/${data.image}`)} alt='How to measure your body' />
    </div>)
      :
      null;
  }

  const renderLegend = (data) => {
    return data && data.content && data.content.length ?
      (<div className='ln-u-push-top ln-u-push-bottom'>
        {data.content.map((howToMeasureContent, index) => {
          const { title, description } = howToMeasureContent;

          return (<div key={index} className='ln-u-push-bottom'>
            <div className='ln-u-font-weight-bold'>{title}</div>
            <div>{description}</div>
          </div>)
        })}
      </div>)
      :
      null;
  }

  const { title, description } = header;

  return (<React.Fragment>
    <div id='howToMeasure' className='ln-u-h4 ln-u-flush-bottom'>{title}</div>
    <div>{description}</div>
    {renderImageMap(data)}
    {renderLegend(data)}
  </React.Fragment>);
}

export default HowToMeasure;
