import React from 'react';
import Accordion from '../../Accordion/Accordion';
import TableWrapper from './TableWrapper';
import Button from './Button';

class Section extends React.Component {

  constructor(props) {
    super(props);
    const { section } = props;
    const { filters } = section;
    this.state = {
      selectedFilter: filters && filters.length ? filters[0].id : ''
    }
  }

  renderTitle = () => {
    const { section } = this.props;
    const { title } = section;
    return title ? (<div className='ln-u-h4'>{title}</div>) : null;
  }

  setSelectedFilter = (selectedFilter) => this.setState({ selectedFilter });

  renderBody = () => {

    const renderFilters = () => {
      const { section } = this.props;
      const { filters } = section;
      const { selectedFilter } = this.state;
      return filters && filters.length ?
        (<div className='ln-u-display-flex ln-u-justify-content-space-between ln-u-soft-top ln-u-soft-bottom'>
          {(filters.map((filter, index) => {
            return (<Button key={index} id={filter.id} text={filter.text}
              selected={filter.id === selectedFilter} onButtonClick={this.setSelectedFilter} />);
          }))}
        </div>)
        :
        null;
    };

    const renderTables = () => {
      const { section } = this.props;
      const { body } = section;
      const { selectedFilter } = this.state;
      return body && body.content && body.content.length ?
        (body.content
          .filter((tableContent) => {
            return selectedFilter ?
              (tableContent.filter && tableContent.filter === selectedFilter)
              :
              (true)
          })
          .map((tableContent, index) => {
            return <TableWrapper key={index} content={tableContent} />;
          }))
        :
        null;
    };

    return (<div className='ln-u-soft-bottom'>
      {renderFilters()}
      {renderTables()}
    </div>);
  }

  render() {
    const { section } = this.props;
    const { open } = section;
    return (<Accordion title={this.renderTitle()} body={this.renderBody()}
      defaultOpen={open} borderStyle={'tu-c-size-guide__accordion'} />);
  }
}

export default Section;
